@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto Slab', serif;
  font-weight: 700;
}

.container{
  max-width: 1440px;
  margin: 0 auto;
}

@media (min-width:1366px){
  .container {
    max-width: 1140px;
    margin: 0 auto;
  }
}
.fon{
  background: #839695;
}

.article{
  padding: 40px 120px 0px 120px;
  text-align: start;
}
.title{
  font-size: 36px;
  color: #fff;
}
.title__img{
  width: 100%;
  max-height: 450px;
  margin-top: 10px;
}
.title__description{
  font-weight: 500;
  font-size: 17px;
  padding: 30px 0;
  }
.comment{
  background: whitesmoke;
  text-align: left;
  padding: 10px;
}
.card{
  width: 100%;
  background: #112B3E;
  margin: 40 0px;
  text-align: center;
  font-weight: 700;
  border: none !important;
  transition-duration: .5s;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  cursor: pointer;
  border: none !important;
  transition-duration: 1s;
}
.mycard{
  color: white;
  font-size: 10px;
  background-color: #112b3e;
  padding: 10px;
  text-decoration: none;
  border: none !important;
  }
a{
    text-decoration: none;
    width: 100%;
 }

.title{
  color: white;
  padding: 5px;
  font-size: 24px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: none !important;
}
.link{
  -webkit-text-decoration-color: white;
          text-decoration-color: white;
  color: white;
  font-size: 10px;
  background-color: #112b3e;
  padding: 10px;
  text-decoration: none;
}




.fon{
  background: #839695;
}
.card-block{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 35px;
  grid-row-gap: 80px;
  padding: 40px 55px;
}
img{
    width: 100%;
  }

.main-header {
  width: 100%;
  background-color: #112b3e;
  padding: 23px 0 13px 27px;
  line-height: 47, 48px;
}
.main-header__name {
  background-color: #112b3e;
  color: white;
}
a {
  background-color: #112b3e;
  text-decoration: none;
  color: white;
}
.main-header__name-domen {
  background-color: #112b3e;
  font-size: 24px;
  line-height: 31, 65px;
}

      
