.card{
  width: 100%;
  background: #112B3E;
  margin: 40 0px;
  text-align: center;
  font-weight: 700;
  border: none !important;
  transition-duration: .5s;
  transform: scale(1.05);
  cursor: pointer;
  border: none !important;
  transition-duration: 1s;
}
.mycard{
  color: white;
  font-size: 10px;
  background-color: #112b3e;
  padding: 10px;
  text-decoration: none;
  border: none !important;
  }
a{
    text-decoration: none;
    width: 100%;
 }

.title{
  color: white;
  padding: 5px;
  font-size: 24px;
  text-align: center;
  display: flex;
  flex-direction: column;
  border: none !important;
}
.link{
  text-decoration-color: white;
  color: white;
  font-size: 10px;
  background-color: #112b3e;
  padding: 10px;
  text-decoration: none;
}



